import React from "react";
import s from "./ThisDayInfo.module.scss";
import cloud from "../../../../assets/images/cloud.png";
import { ThisDayItem } from "./ThisDayItem";
import { Weather } from "../../../../store/types/types";

interface Props {
  weather: Weather;
}

export interface Item {
  icon_id: string;
  name: string;
  value: string;
}

const pressureDescription = (pressure: number): string => {
  if (pressure < 1000) {
    return "";
  } else if (pressure > 1020) {
    return "";
  } else {
    return "";
  }
};

const getWindDirection = (degree: number): string => {
  const directions = [
    "", "-", "", "-",
    "", "-", "", "-"
  ];
  
  const index = Math.round(degree / 45) % 8;
  return directions[index];
};

const getWindClass = (speed: number): string => {
  if (speed < 5) {
    return " ";
  } else if (speed < 10) {
    return " ";
  } else {
    return " ";
  }
};


export const ThisDayInfo = ({ weather }: Props) => {

  const items: Item[] = [
    {
      icon_id: "temp",
      name: "",
      value: `${Math.floor(
        weather.current.temperature_2m
      )} -   ${Math.floor(weather.current.apparent_temperature)}`,
    },
    {
      icon_id: "pressure",
      name: "",
      value: `${
        Math.floor(weather.current.pressure_msl)
      }    - ${(pressureDescription(weather.current.pressure_msl))}`,
    },
    {
      icon_id: "precipitation",
      name: "",
      value: weather.current.precipitation > 1 ? `${weather.current.precipitation} ` : " ",
    },
    {
      icon_id: "wind",
      name: "",
      value: `${Math.floor(weather.current.wind_speed_10m)} / ${getWindDirection(weather.current.wind_direction_10m)} - ${getWindClass(weather.current.wind_speed_10m)}`,
    },
  ];

  return (
    <div className={s.this__day_info}>
      <div className={s.this__day_info_items}>
        {items.map((item: Item) => (
          <ThisDayItem key={item.icon_id} item={item} />
        ))}
      </div>
      <img className={s.cloud__img} src={cloud} alt="" />
    </div>
  );
};
